.notification { 
	color: green;
	padding: .5em 1.5em; 
	background-color: rgb(193, 189, 189);
	border-radius: 2em;
	border-color: green;
	max-width: 30%;
	margin: 1rem 0;
}

.error {
	color: red;
	padding: .5em 1.5em; 
	background-color: rgb(193, 189, 189);
	border-radius: 2em;
	border-color: red;
	max-width: 30%;
	margin: 1rem 0;
}
